import React, { useState, useEffect } from 'react';
import styled, { withTheme } from 'styled-components';

const NavbarContainer = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background-color: ${({ theme }) => theme.bodyBg};
  color: ${({ theme }) => theme.textColor};
`;

const UTCClockContainer = styled.div`
  display: flex;
  align-items: center;
  font-size: 14px;

  .clock-info {
    display: grid;
    grid-template-columns: auto 1px auto 1px auto;
    gap: 8px;
    align-items: center;
    color: ${({ theme }) => theme.textColor};
  }

  .hex-day {
    text-transform: uppercase;  /* Display in capitals */
    color: ${({ theme }) => theme.textColor};
    font-size: 16px;

    @media screen and (max-width: 768px) {
      font-size: 11px;
    }

  }

  .divider {
    width: 1px;
    height: 12px;
    background-color: ${({ theme }) => theme.textColor};
  }

  .date {
    text-transform: uppercase;  /* Display in capitals */
    font-size: 16px;
    color: ${({ theme }) => theme.textColor};

    @media screen and (max-width: 768px) {
      font-size: 11px;
    }

  }

  .utc-time {
    font-size: 16px;
    color: ${({ theme }) => theme.textColor};

    @media screen and (max-width: 768px) {
      font-size: 11px;
    }

  } 

`;

const UTCClock = ({ theme }) => {
  const [clockInfo, setClockInfo] = useState({ hexDay: '', date: '', utcTime: '' });

  useEffect(() => {
    const updateClock = () => {
      const now = new Date();
      const utcString = now.toUTCString();

      const hexStartDate = new Date('2019-12-02T00:00:00Z');
      const hexDay = Math.floor((now - hexStartDate) / (1000 * 60 * 60 * 24)).toString();

      setClockInfo({
        hexDay: `Day ${hexDay}`,
        date: now.toLocaleDateString('en-US', { day: 'numeric', month: 'short', year: 'numeric' }).toUpperCase(),
        utcTime: utcString.slice(17, -4), // Adjusted the slice to only get the time part
      });
    };

    const intervalId = setInterval(updateClock, 1000);

    // Cleanup on component unmount
    return () => clearInterval(intervalId);
  }, []);

  return (
    <NavbarContainer theme={theme}>
      <UTCClockContainer theme={theme}>
        <div className="clock-info">
          <div className="hex-day">{clockInfo.hexDay}</div>
          <div className="divider"></div>
          <div className="date">{clockInfo.date}</div>
          <div className="divider"></div>
          <div className="utc-time">{clockInfo.utcTime} UTC</div>
        </div>
      </UTCClockContainer>
    </NavbarContainer>
  );
};

export default withTheme(UTCClock);
