import React, { useState, useEffect } from 'react';
import ReactPlayer from 'react-player';
import styled from 'styled-components';

const VideoFrame = styled.div`
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 56.25%; /* 16:9 aspect ratio for responsive design on mobile */

  @media screen and (min-width: 768px) {
    max-width: 730px; /* Set a max-width for larger screens */
    margin: 20px auto; /* Center the video on larger screens */
    padding-top: 30%; /* Adjust the padding-top for the desired aspect ratio on PC screens */
  }
`;

const StyledPlayer = styled(ReactPlayer)`
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2)
`;

const VideoComponent = ({ videoUrl, lightMode }) => {
  const [currentVideo, setCurrentVideo] = useState(videoUrl);

  useEffect(() => {
    // Update the current video based on the light mode
    setCurrentVideo(lightMode ? '/hexvid1.mp4' : videoUrl);
  }, [lightMode, videoUrl]);

  return (
    <VideoFrame>
      <StyledPlayer
        url={process.env.PUBLIC_URL + currentVideo}
        controls
        loop={true} // Loop the video
        muted={true} // Start on mute
        playsinline // Enable playback in a non-fullscreen mode on mobile
      />
    </VideoFrame>
  );
};

export default VideoComponent;
