import React, { useState } from 'react';
import styled, { withTheme } from 'styled-components';
import { FiSun, FiMoon } from 'react-icons/fi'; // Import sun and moon icons
import UTCClock from './UTCClock';

const lightTheme = {
  bodyBg: '#e6e6e6', // White background for the light theme
  textColor: '#000000', // Black text color
  linkColor: '#0066cc', // Blue link color
  titleColor: '#000000', // Color for titles in light mode
  dataColor: '#000000', // Color for live data in light mode
  // Add more light mode colors as needed
};

const darkTheme = {
  bodyBg: '#111', // Black background for the dark theme
  textColor: '#e6e6e6', // White text color
  linkColor: '#66ccff', // Light blue link color
  titleColor: '#e6e6e6', // Color for titles in dark mode
  dataColor: '#e6e6e6', // Color for live data in dark mode
  // Add more dark mode colors as needed
};

const NavbarContainer = styled.nav`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  background-color: ${({ theme }) => theme.bodyBg};
  color: ${({ theme }) => theme.textColor};
  
  line-height: 1; /* Set line-height to 1 */
  max-width: 767px;
  margin: 0 auto;
`;

const NavbarContent = styled.div`
  display: flex;
  align-items: center;
  flex-grow: 1; /* Allow NavbarContent to grow and occupy the available space */
  
  border-radius: 12px;
`;

const UTCClockContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  margin-left: 16px;
`;

const IconContainer = styled.div`
  cursor: pointer;
  margin-right: 20px;
`;

const NavBar = ({ theme, toggleMode }) => {
  const [isLightMode, setLightMode] = useState(theme.isLightMode);

  const handleToggle = () => {
    setLightMode(!isLightMode);
    toggleMode();
  };

  return (
    <NavbarContainer theme={isLightMode ? lightTheme : darkTheme}>
      <NavbarContent>
        <UTCClockContainer>
          <UTCClock />
        </UTCClockContainer>
      </NavbarContent>
      <IconContainer onClick={handleToggle}>
        {isLightMode ? <FiMoon size={24} /> : <FiSun size={24} />}
      </IconContainer>
    </NavbarContainer>
  );
};

export default withTheme(NavBar);
