import React, { useState } from 'react';
import styled, { ThemeProvider } from 'styled-components';
import NavBar from './NavBar';
import HexGrid from './HexGrid';
import LiveData from './LiveData';
import LiveData2 from './LiveData2';
import VideoComponent from './VideoComponent';
import StyledImageGalleryComponent from './StyledImageGalleryComponent';
import GlobalStylesComponent from './GlobalStylesComponent';
import 'react-image-gallery/styles/css/image-gallery.css';
import image1 from './images/img1.png';
import image1Thumbnail from './images/imgthumbnail1.png';
import image2 from './images/img2.png';
import image2Thumbnail from './images/imagethumbnail2.png';
import image3 from './images/img3.png';
import image3Thumbnail from './images/imagethumbnail3.png';
import image4 from './images/img4.png';
import image4Thumbnail from './images/imagethumbnail4.png';

const lightTheme = {
  bodyBg: '#e6e6e6',
  textColor: '#000000',
  linkColor: '#0066cc',
  titleColor: '#000000',
  dataColor: '#000000',

};

const darkTheme = {
  bodyBg: '#111',
  textColor: '#e6e6e6',
  linkColor: '#66ccff',
  titleColor: '#e6e6e6',
  dataColor: '#e6e6e6',

};

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
`;

const images = [
  {
    original: image1,
    thumbnail: image1Thumbnail,
    description: 'Get MetaMask',
  },
  {
    original: image2,
    thumbnail: image2Thumbnail,
    description: 'Buy PLS',
  },
  {
    original: image3,
    thumbnail: image3Thumbnail,
    description: 'Swap for HEX',
  },
  {
    original: image4,
    thumbnail: image4Thumbnail,
    description: 'Mine HEX',
  },
  // Add more images as needed
];

const App = () => {
  const [isLightMode, setLightMode] = useState(false);

  const toggleMode = () => {
    setLightMode((prevMode) => !prevMode);
  };

  return (
    <ThemeProvider theme={isLightMode ? lightTheme : darkTheme}>
      <AppContainer className={isLightMode ? 'light-mode' : 'dark-mode'}>
        <GlobalStylesComponent />
        <VideoComponent videoUrl={isLightMode ? '/hexvid1.mp4' : '/hexvid3.mp4'} isLightMode={isLightMode} />
        <LiveData2 />
        <LiveData /> {/* Include the LiveData component */}
        <header>
          <NavBar isLightMode={isLightMode} toggleMode={toggleMode} />
        </header>
        <HexGrid theme={isLightMode ? lightTheme : darkTheme} />
        <StyledImageGalleryComponent images={images} /> {/* Use the StyledImageGalleryComponent */}
      </AppContainer>
    </ThemeProvider>
  );
};

export default App;
