// StyledImageGalleryComponent.js
import React from 'react';
import styled from 'styled-components';
import ImageGallery from 'react-image-gallery';
import 'react-image-gallery/styles/css/image-gallery.css';

const StyledImageGallery = styled(ImageGallery)`
  .image-gallery {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    overflow: hidden;
  }

  .image-gallery-slide {
    text-align: center;
    background: ${({ theme }) => theme.bodyBg};
  }

  .image-gallery-image {
    border-radius: 8px;
    transition: transform 0.5s ease;
    &:hover {
      transform: scale(1.05);
    }
  }

  .image-gallery-thumbnail {
    border: 2px solid transparent;
    border-radius: 8px;
    transition: border 0.3s ease;
    &:hover,
    &.active {
      border: 2px solid ${({ theme }) => theme.linkColor};
    }
  }

  .image-gallery-play-button,
  .image-gallery-fullscreen-button {
    filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.7));
    &:hover {
      color: ${({ theme }) => theme.linkColor};
    }
  }

  .image-gallery-bullets .image-gallery-bullet {
    margin: 0 5px;
    background: ${({ theme }) => theme.textColor};
    &:hover,
    &.active {
      background: ${({ theme }) => theme.linkColor};
    }
  }
`;

const StyledImageGalleryContainer = styled.div`
  text-align: center;
  margin-bottom: 20px; /* Adjust margin bottom as needed */
`;

const StyledTitle = styled.h2`
  color: ${({ theme }) => theme.titleColor}; /* Use the title color from the theme */
  font-size: 24px; /* Adjust font size as needed */
  margin-bottom: 10px; /* Adjust margin bottom as needed */
`;

const StyledImageGalleryComponent = ({ images }) => {
    return (
        <StyledImageGalleryContainer>
            <StyledTitle>HOW TO BUY HEX</StyledTitle>
            <StyledImageGallery
                items={images}
                autoPlay={true} // or autoPlay={4000} for autoplay with a specific interval in milliseconds
            />
        </StyledImageGalleryContainer>
    );
};

export default StyledImageGalleryComponent;
